@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans&family=Permanent+Marker&display=swap);
body {
  margin: 0;
  font-family: 'Balsamiq Sans', cursive;
  font-size: 1.1rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-image: url("");
  background-repeat: no-repeat;
  background-size: cover; */
  background-color: #393e46;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input,select,button{
  font-family: 'Balsamiq Sans', cursive;
}

.search-container{
    height: 11rem;
    grid-template-rows: [start-title] 1fr [end-title start-search] 1fr [end-search];
    grid-template-areas: "title" "search" ;
    background-color: #222831;
    box-shadow:         0px 3px 8px 0px rgba(50, 50, 50, 0.32);
}

.title-container{
    grid-area: "title";
    height: 45%;
    text-align: center;
    padding-top: 1.6rem;
    font-size: 2rem;
    font-weight: bold;
    color: white;
}

.search-inputs{
    display: flex;
    justify-content: center;
}

.search-inputs *{
    margin-right: 1rem;
    border-radius: 5px;
    font-size: 1rem;
    border-color: white;
}

.search-inputs > select, button{
    cursor: pointer;
}

.results-container{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    height: 32rem;
    padding-top: 1.7rem;
    /* background-image: url("");
    background-repeat: no-repeat;
    background-size: cover; */
}

.loader-container{
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    height: 32rem;
    width: 100%;
}

.loader {
  border: 16px solid #f3f3f3;
  border-top: 16px solid #3498db;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
  z-index: 1;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
.usercard{
    margin: 0 1rem 1.7rem 1rem;
    background-color: transparent;
    perspective: 1000px;
    cursor: pointer;
}

.usercard-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.6s;
    transform-style: preserve-3d;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}

.usercard:hover .usercard-inner {
    transform: rotatex(180deg);
}

.usercard-front, .usercard-back {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    border: 2px solid white;
    border-radius: 5px;
    display: flex;
    justify-content: space-evenly;
    padding: 1rem;
}

.usercard-front {
    background-color: #00adb5;
    color: black;
}

.usercard-back {
    position: absolute;
    bottom: 0;
    width: calc(100% - 2rem);
    background-color: #2980b9;
    color: white;
    transform: rotatex(180deg);
}
.profile{
    background-color: #00adb5;
    width: 25rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem 0;
    border: 5px solid black;
    border-radius: 20px;
    box-shadow:         0px 5px 12px 0px rgba(50, 50, 50, 0.64);
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url("https://previews.123rf.com/images/vladgrin/vladgrin1303/vladgrin130300041/18540540-vector-de-fondo-se-compone-de-muchos-iconos-de-los-humanos-modernos-el-concepto-de-comunicaci%C3%B3n-entre-l.jpg");
}

.profile-container{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    /* background-repeat: no-repeat;
    background-size: cover;
    background-image: url(""); */
}

.user-avatar{
    width: 15rem;
    height: 15rem;
    border-radius: 10px;
    border: 2px solid black;
    background-color: #d1d0d0;
}
